.logement_container{
    color: $red;
    margin: 30px 100px 20px;

    @media screen and (max-width:$mobile){
        margin: 15px 20px 27px;
    }

    .logement_fiche{
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;

        @media screen and (max-width:$mobile){
            flex-direction: column;
            margin-bottom: 0px;
        }
    }

    .logement_title{
        h2{
            margin: 0;
            @media screen and (max-width:$mobile){
                font-size: 18px;
            }
        }
        p{
            margin-top: 0px;
            @media screen and (max-width:$mobile){
                font-size: 14px;
                margin: 8px 0;
            }
        }
    }

    .logement_collapse{
        display: flex;
        justify-content: space-between;
        @media screen and (max-width:$mobile){
            flex-direction: column;
            align-items: center;
            gap: 20px;
        }
    }
}

.logement_tag__container{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.logement_tag{
    color: white;
    height: 25px;
    background-color: $red;
    border-radius: 10px;
    padding: 0px 20px;
    text-align: center;
    line-height: 25px;
    @media screen and (max-width:$mobile){
        font-size: 10px;
        height: 18px;
        line-height: 18px;
    }
}
.logement_host_container{
    @media screen and (max-width:$mobile){
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        position: relative;
        height: 60px;
    }
}

.logement_host{
    display: flex;
    gap: 10px;
    @media screen and (max-width:$mobile){
        height: 32px;
        margin-top: 12px;
    }
    p{
        height: 52px;
        width: 93px;
        text-align: end;
        @media screen and (max-width:$mobile){
            font-size: 12px;
            margin: 0px;
            height: 32px;
            line-height: 32px;
        }
    }
    img{
        width: 64px;
        height: 64px;
        border-radius: 32px;
        @media screen and (max-width:$mobile){
            width: 32px;
            height: 32px;
            border-radius: 16px;
        }
    }
}

.logement_rate{
    text-align: end;
    @media screen and (max-width:$mobile){
        line-height: 60px;
    }
}

.star{
    color: $grayRate;
}